<template><div class="content"><h1>关于<a class="header-anchor" id="%E5%85%B3%E4%BA%8E" href="#%E5%85%B3%E4%BA%8E">#</a></h1>
<p>这里是关于页面，你可以在这里了解到关于我和关于这个博客的相关内容。</p>
<h2>我<a class="header-anchor" id="%E6%88%91" href="#%E6%88%91">#</a></h2>
<p>关于我，大概有这么一些事情</p>
<ul>
<li>目标全栈 —— 学点前端，学点后端，再学点底层硬件和代码，人生结束了~🎉</li>
<li>排版强迫 —— 对排版具有一定的敏感性和强迫症。</li>
<li>切图大师 —— 会使用 Illustrator 和 Photoshop 进行<em>简单的</em>图片处理和图形设计。</li>
<li>质感设计 —— 十分欣赏谷歌的 Material Design 设计体系。</li>
<li>虚假果粉 —— 我的评价是：除了贵都挺好的。</li>
<li>不会听歌 —— 其实就算是喜欢听的歌，听多了我也会不爱听。流派大部分为 Alternative，但有渐渐转向 Indie Rock 的趋势~
<ul>
<li>认识的第一位歌手：Lady Gaga</li>
<li>比较欣赏的歌手：St. Vincent</li>
<li>喜欢的乐队：Arctic Monkeys, The Black Keys, Broken Bells, Glass Animals, Saint Motel</li>
<li>平时听的其它歌手/乐队：Penelope Scott, Arcade Fire, The Score, Skott, Soler（粤语）, Stealing Sheep, 陈靖霏</li>
<li>我扮演一个路人粉的角色：Taylor Swift, Lana Del Ray, 朴树, 草东没有派对, 美秀集团</li>
</ul>
</li>
<li>是方块人 —— Minecraft 10 年玩家。</li>
<li>伪二次元 —— 可能会看那么几部番。</li>
<li>超敏星座 —— 在<a href="https://www.16personalities.com/ch/%E4%BA%BA%E6%A0%BC%E6%B5%8B%E8%AF%95" class="external-link" target="_blank" rel="noopener noreferrer">十六星座<span class="external-link-icon mdi mdi-launch"></span></a>中，我是
INFP-T（2019、2021、2023 年测试结果）。</li>
<li>あいうえ —— 日语初级兴趣爱好者。</li>
<li>酒精过敏 —— 喝酒脸红没感觉，大概就是不能喝酒吧<sup class="footnote-ref"><a href="#fn1" id="fnref1">[1]</a></sup>。</li>
</ul>
<h2>此博客<a class="header-anchor" id="%E6%AD%A4%E5%8D%9A%E5%AE%A2" href="#%E6%AD%A4%E5%8D%9A%E5%AE%A2">#</a></h2>
<p>本博客始创于 2019 年 8 月 16 日，使用 <a href="https://vuepress.vuejs.org" class="external-link" target="_blank" rel="noopener noreferrer">VuePress<span class="external-link-icon mdi mdi-launch"></span></a> 搭建，免费运行在 GitHub Pages 上，运行成本只有域名的成本。起初是手动 build+push 部署，后来使用 GitHub Actions 自动部署。</p>
<p>2023 年 7 月 15 日停止使用 VuePress，并仿照原有的构建模式，使用 Vue 3 对博客进行了重写。目前博客自动部署在 Vercel 上。</p>
<p>我大概从 2016 年的 12 月份开始写博客，其实一开始的动机就是<em>跟风</em>。当初并不会这些东西，或者说完全没有概念，但是在摸索之下还是把博客搭起来了。一开始的时候没有什么构思，所以并没有写出什么有价值的内容，大多数是一些流水账，比如看过的电影的感受（但现在内容已完全丢失），或者是对博客本身维护的记录等等。</p>
<p>这几年里先后换用了 WordPress、Typecho、Hugo、Hexo 等博客程序，经历了反复删库。由于内容并没有太多价值，所以一直不是很放在心上。直到有一天，廉价服务商的不可靠导致数据的永久丢失，然而那时却已经产出了一些我比较珍惜的内容。以此为契机，又为了追求简洁，我萌生了利用纯静态博客程序的想法。</p>
<p>博客的主题参考了 VuePress 默认主题。本博客域名 <a href="https://subilan.win" class="external-link" target="_blank" rel="noopener noreferrer">subilan.win<span class="external-link-icon mdi mdi-launch"></span></a> 长期不变，你也可以通过 <a href="https://subilan.vercel.app" class="external-link" target="_blank" rel="noopener noreferrer">subilan.vercel.app<span class="external-link-icon mdi mdi-launch"></span></a> 来访问。</p>
<p>为了便于查看，加入了字数统计和阅读时间的估算，具体算法如下。</p>
<div class="language-javascript"><pre class="language-javascript"><code class="language-javascript"><span class="token keyword">function</span> <span class="token function">countWords</span><span class="token punctuation">(</span><span class="token parameter">text</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
    <span class="token keyword">return</span> <span class="token punctuation">(</span>text<span class="token punctuation">.</span><span class="token function">match</span><span class="token punctuation">(</span><span class="token regex"><span class="token regex-delimiter">/</span><span class="token regex-source language-regex">[\u00ff-\uffff]|\S+</span><span class="token regex-delimiter">/</span><span class="token regex-flags">g</span></span><span class="token punctuation">)</span> <span class="token operator">||</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">.</span>length<span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre></div>
